@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #edf5fe;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
:root{
  --mainColor: #03a9f4;
}
.mainContainer{
    height: 65vh;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap:10px;
    gap:10px;
    transition: all 0.4s ease-in-out;
}
.headingContainer{
  padding: 20px 35px;
}
.heading{
    font-family: 'Dancing Script', cursive;
    font-size: 45px;
    color: #00bcd4;
    font-weight: 600;
    text-shadow: -3px 15px 10px #00000026;
}
.dropZoneContainer{
    margin: 8px;
    width: 500px;
    height: 300px;
    padding: 20px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 14px 19px 0px #80808066;
    transition: all 0.4s ease-in-out;
}
.dropZone{
    border: 2px dashed #03a9f4;
    border: 2px dashed var(--mainColor);
    height: 100%;
    border-radius: 10px;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease-in-out;
}
#fileInput{
  display:none;
}
.browseBtn{
  color:#03a9f4;
  color:var(--mainColor);
  font-weight: 500;
  cursor: pointer;
}
.iconContainer{
  height:60%;
  display:flex;
  justify-content: center;
}
.uploadIcon{
  height:100%;
}
.imageContainer{
  width: 500px;
  margin-left: 30px;
}
.outerImg{
  height: 100%;
  width: 100%;
  -webkit-filter: hue-rotate(287deg);
          filter: hue-rotate(287deg);
}
.downloadLink{
  display:flex;
  width:95%;
  align-items: center;
  margin-top: 5px;
  transition: all 0.4s ease-in-out;
}
.link{
    border: 2px dashed #03a9f4;
    border: 2px dashed var(--mainColor);
    width: 97%;
    border-radius: 4px;
    padding: 7px;
    margin: 5px;
    outline: none;
    font-size: 15px;
}
.copyIcon{
  height: 25px;
  -webkit-filter: opacity(0.6);
          filter: opacity(0.6);
  cursor: pointer;
}
.loading{
    height: 28px;
    background-color: #39b29d;
    border-radius: 20px;
    margin-top: 18px;
    text-align: left;
    padding: 2px 10px;
    transition: all 0.4s ease-in-out;
    color: white;
    box-shadow: -1px 11px 9px 3px #aca7a785;
    align-self: flex-start;
}

@media only screen and (max-width: 480px) {
  .imageContainer, .outerImg{
    display:none;
  }
  
}

.downloadContainer{
  height:75vh;
  display:flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    width: 410px;
    height: 450px;
    grid-gap: 9px 0px;
    gap: 9px 0px;
    border-radius: 20px;
    box-shadow: 0px 13px 13px 2px #0000002e;
}

.downloadBtn{
    padding: 10px 32px;
    border-radius: 11px;
    background-color: #03a9f4;
    background-color: var(--mainColor);
    color: white;
    font-weight: 500;
    font-size: 18px;
    border: none;
    box-shadow: 0px 6px 11px 2px #00000038;
    outline: none;
    margin-top: 20px;
}

.title{
  margin: 15px 0;
}
.fileName{
  font-weight: 700;
}
.downloadIcon{
  height: 170px;
}
